 
import React, {useState} from 'react'
import { Row,Col,Dropdown} from 'react-bootstrap'
import { Range } from 'rc-slider'
import 'rc-slider/assets/index.css'
import ReactSelect from '../../../Global/App/ReactSelect/ReactSelect'
import {propertyType,bathrooms,bedrooms,builtuparea,yearbuilt} from '../../../Global/App/ReactSelect/SelectData'
const AdvanceSearchOptionHome5=({ClassName,style2,textthm3,btnthm3})=>{
    const priceSlider = (value) => {
        document.getElementById("amount").value = "$" + value[0];
        document.getElementById("amount2").value = "$" + value[1];
    }

return(
        <form className="home5_advanced_search_form">
            <div className="form-group">
                <input type="text" className="form-control" id="exampleInputName1" placeholder="Enter keyword..."/>
            </div>
            <div className="form-group">
                <div className="search_option_two">
                    <div className="candidate_revew_select">
                    <ReactSelect data={propertyType} tickColor='#ff5a5f' style2={textthm3} />
                    </div>
                </div>
            </div>
            <div className="form-group df">
                <input type="text" className="form-control" id="exampleInputEmail" placeholder="Location"/>
                <label htmlFor="exampleInputEmail"><span className="flaticon-maps-and-flags"></span></label>
            </div>
            <div className="form-group">
                <div className="small_dropdown2 home5">
                    <Dropdown>
                        <Dropdown.Toggle variant="custom" id="prncgs" className="dd_btn">
                            <span>Price</span>
                            <span className="priceBtnIn"><span className="fa fa-angle-down "></span></span>
                        </Dropdown.Toggle>

                        <Dropdown.Menu className={ClassName+" dd_content2"}>
                            <div className="pricing_acontent">
                                <input type="text" id="amount" className="amount" placeholder="$3000" /> 
                                <input type="text" id="amount2" className="amount2" placeholder="$18000" />
                                <Range min={2000} max={18990} defaultValue={[3000, 18000]} onChange={priceSlider} />
                            </div>
                        </Dropdown.Menu>
                    </Dropdown>
                </div>
            </div>
            <div className="form-group custome_fields_520">
                <div className="navbered">
                    <div className="mega-dropdown home5">
                        <Dropdown>
                    <Dropdown.Toggle  className="dropbtn" variant="custom">
                            Advanced <i className="flaticon-more pl10 flr-520"></i>
                    </Dropdown.Toggle>

                    <Dropdown.Menu className={ClassName+" dropdown-content"}>
                        <Row className="p15">
                            <Col lg={12}><h4 className="text-thm3">Amenities</h4></Col>
                            <Col >
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck1" />
                                            <label className="custom-control-label" htmlFor="customCheck1">Air Conditioning</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck2" />
                                            <label className="custom-control-label" htmlFor="customCheck2">Lawn</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck3" />
                                            <label className="custom-control-label" htmlFor="customCheck3">Swimming Pool</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck4" />
                                            <label className="custom-control-label" htmlFor="customCheck4">Barbeque</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck5" />
                                            <label className="custom-control-label" htmlFor="customCheck5">Microwave</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck6" />
                                            <label className="custom-control-label" htmlFor="customCheck6">TV Cable</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck7" />
                                            <label className="custom-control-label" htmlFor="customCheck7">Dryer</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck8" />
                                            <label className="custom-control-label" htmlFor="customCheck8">Outdoor Shower</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck9" />
                                            <label className="custom-control-label" htmlFor="customCheck9">Washer</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck10" />
                                            <label className="custom-control-label" htmlFor="customCheck10">Gym</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck11" />
                                            <label className="custom-control-label" htmlFor="customCheck11">Refrigerator</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck12" />
                                            <label className="custom-control-label" htmlFor="customCheck12">WiFi</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col>
                                <ul className="ui_kit_checkbox selectable-list">
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck13" />
                                            <label className="custom-control-label" htmlFor="customCheck13">Laundry</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck14" />
                                            <label className="custom-control-label" htmlFor="customCheck14">Sauna</label>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customCheck15" />
                                            <label className="custom-control-label" htmlFor="customCheck15">Window Coverings</label>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                        </Row>
                        <Row className="p15 pt0-xsd">
                            <Col xl="10" lg="11">
                                <ul className="apeartment_area_list mb0">
                                    <li className="list-inline-item mb10" >
                                        <div className="candidate_revew_select">
                                            <ReactSelect data={bathrooms} tickColor='#ff5a5f' style2={textthm3}/>

                                        </div>
                                    </li>
                                    <li className="list-inline-item mb10">
                                        <div className="candidate_revew_select">
                                            <ReactSelect data={bedrooms} tickColor='#ff5a5f' style2={textthm3}/>
                                        </div>
                                    </li>
                                    <li className="list-inline-item mb10">
                                        <div className="candidate_revew_select">
                                           <ReactSelect data={yearbuilt} tickColor='#ff5a5f' style2={textthm3}/>
                                        </div>
                                    </li>
                                    <li className="list-inline-item mb10">
                                        <div className="candidate_revew_select">
                                            <ReactSelect data={builtuparea} tickColor='#ff5a5f' style2={textthm3}/>
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col xl="2" lg="1"></Col>
                        </Row>
                    </Dropdown.Menu>
                </Dropdown>
            
                </div>
                </div>
            </div>
            <div className="search_option_button home5">
                <button type="submit" className="btn btn-block">Search</button>
            </div>
        </form>
        )
}
export default AdvanceSearchOptionHome5
